import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
	data() {
		return {
				lastMoney:0,
				paymentPassword:'',
				inputAmount:'',//金额
				amountList:[10,50,100],//预设3个可选快捷金额
				paytype:'alipay'//支付类型
			};
		},
		onLoad(option) {
			console.log(option)
			this.lastMoney = option.lastMoney;
		},
		onShow() {
			let that = this;
			uni.getStorage({
				key:'payment_password',
				success: (res) => {
					console.log('payment_password'+res.data)
					that.paymentPassword = res.data;
				}
			})
		},
		methods:{
			select(amount){
				this.inputAmount = amount;
			},
			doDeposit(){
				if (parseFloat(this.inputAmount).toString() == "NaN") {
					uni.showToast({title:'请输入正确金额',icon:'none'});
					return ;
				}
				if(this.inputAmount<=0){
					uni.showToast({title:'请输入大于0的金额',icon:'none'});
					return ;
				}
				if(parseFloat(this.inputAmount).toFixed(2)!=parseFloat(this.inputAmount)){
					uni.showToast({title:'最多只能输入两位小数哦~',icon:'none'});
					return ;
				}
				if(!this.paymentPassword){
					console.log('payment'+this.paymentPassword)
					uni.navigateTo({
						url:'../setting/userInformation/paymentPassword'
					})
					return ;
				}
				//模板模拟支付，实际应用请调起微信/支付宝
				uni.showLoading({
					title:'支付中...'
				});
				let that = this;
				console.log(that);
				uni.request({
					url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/invest/money', //充值
					method: 'PUT',
					data: {
						userId: that.userId,
						money:that.inputAmount
					},
					success: res => {
						console.log(res.data.data);
						that.lastMoney = res.data.data;
						uni.showToast({
							title:'支付成功'
						});
					}
				});
				setTimeout(()=>{
					uni.hideLoading();

					setTimeout(()=>{
						uni.redirectTo({
							url:'../../pay/success/success?amount='+this.inputAmount
						});
					},300);
				},700)
			}
		},
	}
